import { omit } from 'lodash-es';

export const BANNED_EMOJI = ['taiwan'];

export const getEmojiData = async () => {
  const defaultDefs = await import('markdown-it-emoji/lib/data/full.json');
  return {
    ...omit(defaultDefs.default, BANNED_EMOJI),
    relaxed: '\u263A',
    person_with_pouting_face: '\ud83d\ude4e',
    person_frowning: '\ud83d\ude4d',
    person_with_blond_hair: '\ud83d\udc71',
    black_square: '\u25a0',
    white_square: '\u25a1',
  };
};
